.climaFrio {
  max-width: 100%;
  height: 100%;
  border-radius: 15px;
  background: #667db6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #667db6, #0082c8, #76badf, #ffffff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.climaCaluroso {
  max-width: 100%;
  height: 100%;
  border-radius: 15px;
  background: #00c3ff;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #ffffff, rgb(241, 241, 149), rgb(241, 241, 149),#ffff1c, #ffff1c);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #ffffff, rgb(241, 241, 149), rgb(241, 241, 149), #ffff1c, #ffff1c); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.calurosoNoche {
