.contenedor {
  border: rgba(63, 63, 63, 0.507) solid 1px;
  display: flex;
  flex-direction: column;
  width: 320px;
  height: 200px;
  position: relative;
  border-radius: 15px;
  margin-top: 25px;
  box-shadow: 0 5px 5px 5px hsl(30, 4%, 55%);
  margin-left:25px;
}

.info {
  display: flex;
  justify-content: space-around;
  margin-top: auto;
  margin-bottom: auto;
}

.text {
  text-align: end;
  color: rgb(6, 6, 124);
}

.boton {
  height: min-content;
  width: min-content ;
  background-color: rgb(221, 52, 52);
  position: absolute;
  margin-top:2px;
  right: 2px;
  border-radius: 5px;
  border: none;
  color: white;
}

.cardImg {
  height: 150px;
}

.linkText {
  text-decoration: none !important;
}

.linkText :hover {
  font-size: 30px;
  position: relative;
}
